/*eslint-disable*/module.exports={messages:{"$MYRIA Token":"$미리아 토큰\n","$MYRIA is the native governance and utility token that encapsulates the Myria ecosystem. Players will be able to use $MYRIA to buy, trade, upgrade, vote and more!":"$MYRIA는 Myria 생태계를 압축하는 토착 관리 및 유틸리티 토큰에 해당된다. 선수들은 $MYRIA를 사용하여 구매, 거래, 업그레이드, 투표 및 그 이상을 할 수 있을 것이다.","100% Ethereum-level security guarantees; whilst increase transaction speed and throughput":"100% 이더리움 수준의 보안 보장;반면에 거래 속도 및 처리량 증대\n","2 CPU Cores":"2 CPU 코어","60 GB Disk Space":"60 GB 디스크 공간","<0/> creates a range of free-to-play AAA games spans across an entire interconnected ecosystem, built with the new standard of interoperable NFTs.":"<0/>은 상호운용적 NFT 신규 표준으로 수립되어, 전체 상호 연결된 생태계를 넘어 일정 범위의 부분유료화 AAA게임 기간을 창출합니다.\n","<0>$MYRIA</0> Node & Token coming soon!":"<0>$미리아</0> 노드 & 토큰 개봉 박두! \n","<0>Be prepared as this might be the key to survival in the Myriaverse.</0>":"<0>Be prepared as this might be the key to survival in the Myriaverse.</0>","<0>If it’s yours, it stays yours</0> - You’ve worked hard to build that neat graveyard farm for two whole days and now it’s yours to stay… forever!":"<0>그것이 당신의 것이라면, 그것은 당신의 것에 머물게 됩니다.</0> - 당신은 그렇게 깔끔한 묘지를 이틀 내내 만들기 위해 열심히 작업한 결과 이제 그것은 머물 수 있는 당신의 것이 됩니다….영원히!\n","<0>In order to run any blockchain, including the Myria chain, nodes are required for verification of network transactions. Nodes in the Myria ecosystem will be used for a variety of network-supporting activities including initial test networks, block production and validation.</0><1>In addition to their role as potential validators, node owners will also be asked to - and rewarded for auxiliary blockchain network functions as the Myria ecosystem becomes more advanced. These include the need for decentralized storage of certain critical information in the Myria ecosystem, oracles, governance voting, as well as voting on game-specific concepts.</1>":"<0> Myria 체인을 포함하여 모든 블록 체인을 가동하기 위해, 네트워크 거래 인증 목적 노드가 필수적이다. 생태계 노드는 최초 시험 네트워크, 블록 생산 및 인증을 포함 다양한 네트워크 지원 활동에 사용될 것이다. </0><1> 잠정적 검증자로서의 역할 외에도, 노드 소유자들은 또한 Myria 생태계가 더욱 향상됨에 따라 보조 블록 체인 네트워크 기능을 하도록 요청을 받고 또 보상을 받게 될 것이다. 이에는 Myria 생태계, 신탁, 관리 투표, 그 외 게임 특정 개념에 관한 투표의 특정 주요 정보에 대해 분산된 저장을 할 필요가 포함된다.</1>","<0>Interoperable</0> & Interconnected NFTs":"<0>상호 정보 교환 가능</0> & 상호 연결된 NFTs","<0>Myria node operators will receive daily $MYRIA token rewards for the nodes work.</0><1>Node owners will also be rewarded with NFT drops and tokens based on their reputational score as node operators and general community members.</1><2>Reputation scores will be determined by the playing activity of node owners, the number of tokens they own and stake, previous uptime metrics, and their history of network support (e.g. validation or oracle provider).</2>":"<0>노드 운영자는 매일 노드 작업에 대한 보상으로 $MYRIA 토큰을 받게 될 것이다. </0><1>노드 소유자들은 또한 노드 운영자 및 일반 커뮤니티 회원으로 자신의 명성 점수에 기반한 NFT 드롭 및 토큰으로 보상받게 될 것이다. </1><2>명성 점수는 노드 소유자의 플레이 활동, 그들이 소유하고 걸게 되는 토큰 수, 이전 가동시간 측정 수 및 네트워크 지원 이력(예를 들면 인증 혹은 신탁 제공자)에 의해 결정될 것이다.</2>","<0>Once the Sigils are active, they will be minted directly on the Myria</0>":"<0>Once the Sigils are active, they will be minted directly on the Myria</0>","<0>Play and earn</0> - Convert your score and time spent into tangible rewards (when Beta kicks in). Addictive fun aside, not a single day of gameplay will be time wasted.":"<0>플레이 그리고 벌기</0> - 당신의 점수와 보낸 시간을 유형의 보상으로 전환할 것. (베타가 시작하면) 중독에 빠질 만한 재미가 옆에 있고, 게임플레이 한 지 단 하루도 지나지 않은 스토리가 낭비한 시간이 될 것입니다.\n","<0>Play</0> and earn <1/> rewards and NFTs":"<0>플레이</0> 그리고 벌기 <1/>보상 및 NFTs\n","<0>Resourcefulness rules!</0> - Overcome your competition with the best strategy and become the master of Moonville.":"<0>풍부한 자원 원칙!</0> - 최상의 전략으로 자신의 경쟁을 극복하여 문 빌(Moonville)의 마스터가 되기 바랍니다.\n","<0>Sigils are going to be activated later in time when the mysterious Sigma mission is set into motion.</0>":"<0> 시길스는 미그마 미션이 모션에 설정될 때까지 비활성입니다.</0>\n","<0>Sigils are inactive until the Sigma mission is set into motion.</0>":"<0>Sigils are inactive until the Sigma mission is set into motion.</0>","<0>T-minus 47 gigaquarks</0>":"<0>T-마이너스 47 기가퀄크 </0>\n","<0>You can choose whether you prefer to run your node in the cloud or locally.</0><1>Myria node software will be available to download on Windows, Mac and Linux. Our node operation is not heavily resource-intensive like traditional cryptocurrency mining. You’ll be able to run our node software from your home computer with the following minimum requirements:</1>":"<0> 당신은 클라우드나 현지에서 당신의 노드 운영을 선호할 지 선택할 수 있습니다. </0><1>미리아 노드 소프트웨어는 윈도우, 맥 그리고 리눅스에서 다움로드하여 사용할 수 있습니다. 우리의 노든 운영은 기존의 암호 화폐 채굴처럼 심하게 자원 집약적이지는 않습니다. 당신은 다음의 최소 요건으로 집의 컴퓨터에서 우리의 노드 소프트웨어를 가동할 수 있을 것입니다:</1>\n","A <0>decentralized ecosystem</0> of blockchain games and worlds":"블록 체인 게임 및 세상의 <0>분권화된 생태계 </0> ","A Stable Internet Connection":"안정적인 인터넷 연결","A battle-royale shooter game":"베틀 로얄 슈팅 게임","A competitive play-and-earn tycoon farming simulator.":"경쟁력이 있는 플레이-및-벌기 타이쿤 농장 시뮬레이터.\n","A decentralised Ethereum scaling platform, purpose built for digital assets and gaming.":"분권화된 이더리움 스케일링 플랫폼, 디지털 자산 및 게이밍으로 세워진 목적.\n","A dynamic, entreprenurial executive with over 8+ years in management consulting, focusing on organisational transformations for global companies such as Verizon, Vanguard, Kayo Streaming and more. More recently founding aliens.com.":"베리 존, 뱅가드, 카요 스트리밍 그 이상의 글로벌 기업들을 대상으로 조직 변형에 집중하는 관리 컨설팅에 8년 이상의 동적이고 기업가적인 마인드를 갖춘 임원진.\n","A hilarious multiplayer game that will have your sides splitting as you bounce, tumble, and bowl over your friends and foes. Battle it out through increasingly wacky landscapes and claim your spot in the hall of glory.":"당신이 튀고, 비틀거리며 당신의 친구들과 적에 대해 공을 전질 때, 당신 편들이 쪼개어 지게 될 아주 재미있는 멀티플레이어 게임. 점점 더 익살스러운 풍경을 통하여 끝까지 싸워 명예의 전당에 당신의 입지를 세울 것.\n","A mixture of both uppercase and lowercase letters":"대문자 및 소문자 혼합\n","A mixture of letters and numbers":"글자와 숫자의 혼합\n","A multiplayer go-kart racing game":"멀티플레이어 고-카트 레이싱 게임","A multiplayer obstacle course game":"멀티플레이어 장애물 코스 게임","A rewarding experience":"보람 있는 경험\n","A serial entrepreneur and Web3 proponent with over 15 years of founding experience including four multinational VC-backed companies in traditional tech. More recently founding aliens.com and a blockchain SAAS platform.":"전통 기술에 있어 4개의 북수 국가 VC-후원 기업들을 포함하여 15년 이상의 주조 경험이 있는 일련의 기업가 및 웹3 제안자. 좀 더 최신 주조 aliens.com 및 블록 체인 SAAS 플랫폼.\n","AJ has over 20 years of multidisciplinary design experience— from web and apparel design, to crafting brand identities. He has worked with global brands including Nike, Adidas and Microsoft, among others.":"AJ는 20년 이상의 여러 학문 분야에 걸친 종합적 경륜을 갖추고 있음 – 웹 및 의상 디자인부터 브랜드 정체성 크래프팅까지. 그 중에서도, 그는 나이키, 아디다스 및 마이크로소프트를 포함한 글로벌 브랜드와 작업을 해 왔다.","About":"에 관하여","About the game":"게임에 관하여\n","Access node dashboard":"접속 노드 대시보드\n","Access ongoing education and technical support to ensure a successful onboarding onto the blockchain.":"블록 체인으로의 성공적인 탑승을 보장하고자 지속적인 교육 및 기술 지원 접속","All":"전체\n","Alliance Sigil NFT Launch":"Alliance Sigil NFT Launch","Already have an account?":"계정이 이미 있음?\n","An end-to-end solution for token-based game economies and NFTs that benefits the community":"커뮤니티에 이익을 주는 토큰 기반 게임 경제 및 NFT 목적에 맞는 솔루션","An objective-based shooter game":"목적-기반 슈팅 게임","An x86/X64 Processor":"x86/X64 프로세서","Andrew has been a tech executive in the gaming industry with a career spanning over 27 years. He has worked with some of the most recognizable names in the industry, including Activision, Electronic Arts, 2K, Ubisoft, Sony, and Midway.":"앤드류(Andrew)는 27년 이상 경력의 게임 산업 분야 기술 이사이다. 그는 액티비젼, 일렉트로닉 아트, 2K, 유비소프트, 소니 및 미드웨이를 포함하여 산업의 가장 인정받을 만한 몇몇 이름들과 함께 작업해 오고 있다.","Applies to all videos":"모든 동영상에 적용됨","Architecture":"아키텍처\n","Are you ready for a crazy ride that is nothing short of hilarious? Hop into your shiny kart and race till your wheels burn. Can you handle the heat and destroy your opponents before they throw you off the track?":"당신은 재미로 만땅인 미친 타기할 준비가 되어 있나요? 당신의 반짝거리는 카트로 껑충 뛰어들어 당신의 바퀴가 탈 때까지 경주하기 바랍니다. 당신은 스스로 트랙에서 벗어나기 전에 흥분을 조절하고 적들을 파괴할 수 있나요?\n","Art Creative Lead":"아트 창의적 리드","At Myria, we believe NFTs should not only endow players with verifiable ownership, but also allow players to take their NFTs from one game to another.":"Myria에서 우리는 NFT가 플레이어들에게 입증 가능한 소유권을 제공할 뿐 아니라, 플레이어들이 모든 게임에서 자신들의 NFT를 취하도록 허용하여야 한다.","At least 8 characters—the more characters, the better":"적어도 8개의 캐릭터 - 캐릭터가 많으면 많을수록 더 좋습니다.\n","Autoplay":"오토플레이","Available to mint":"주조 이용 가능\n","BACK TO MYRIA HOME":"미리아 홈으로 돌아가기\n","BACK TO SIGIL DASHBOARD":"시길 대시보드로 돌아가기\n","BUY A NODE":"노드 구매","Battle with style!":"스타일 넘치는 전투!\n","Be rewarded for your contribution":"당신의 기여로 보상을 받을 것\n","Become a Myria node owner & receive rewards":"미리아 노드 소유자가 되어 & 보상 받기\n","Become an integral part of the Myria ecosystem and reap the benefits of your contribution. Early founder node operators receive preferential pricing, which increases as nodes are sold.":"Myria 생태계의 일체가 되고 당신의 기여 이익을 수확함. 초기 창설자 노드 운영자들은 우선 가격 책정을 받는데, 이는 노드가 판매됨으로 인해 증가됨.","Begin your adventure, choose your alliance and claim your free NFT rewards":"Begin your adventure, choose your alliance and claim your free NFT rewards","Blockchain consultancy":"블록 체인 컨설턴시\n","Blockchain infrastructure for Game Studios":"게임 스튜디오의 블록 체인 기반시설","Blockchain platform for game studios":"게임 스튜디오의 블록 체인 플랫폼","Brendan brings a decade of experience in blockchain. He has founded an escrow solution on the Bitcoin blockchain, a crypto ETF on ethereum, and was blockchain lead for the world’s largest trade finance blockchain network.":"브랜든(Brendan)은 블록 체인에서 10년간의 경험을 가지고 있다. 그는 비트코인 블록 체인, 이더리움 암호화폐 ETF 에스크로 솔루션을 창설하였고 세계 최대 거래 금융 블록 체인 네트워크의 블록 체인 리드였다.","Bringing <0>blockchain gaming </0>to the world":"가져오기 <0>블록 체인 게임하기 </0>세상에\n","Brock is an entreprenurial product lead with over 10+ years experience working in the tech startups and founded his own MarTech company. More recently, he helped scale one of the largest blockchain companies focusing on product tokenomics and growth.":"브록(Brock)은 기술 스타트업에서 일하며 10년 이상의 경륜을 갖춘 기업가 제품 리드이고 자신의 마르텍 기업을 설립하였다. 더 최근에는, 그는 제품 토큰노믹스 및 성장에 집중하는 최대 블록 체인 기업들 중의 하나를 키우는데 일조하였다.","Build your blockchain game with Myria":"미리아와 함께 당신의 블록 체임 게임을 만드세요.\n","Build, upgrade, and earn!":"만들고 업그레이드하고 버는다!\n","Building towards a decentralized and permissionless network":"분권화 되고 허용 불가한 네트워크 수립\n","Buying a node license will give the owner the right to run the node (either locally or in the cloud) and receive rewards as determined by governance.":"노드 라이선스 구매는 소유자에게 노드를 가동할 권리(장소상으로나 클라우드에서)를 주고 관리 방식으로 결정됨에 따라 보상을 받게 될 것이다.","CLAIM NOW":"지금 요청하기\n","CLAIM REWARDS":"보상 청구하기\n","CLAIMED":"CLAIMED","CMO":"CMO\n","COLLECT NOW":"지금 회수하기\n","COMING SOON":"개봉 박두\n","CONNECT WALLET":"지갑 연결하기\n","COPIED":"복사됨\n","COPY LINK":"링크 복사\n","CPO (People)":"CPO(피플)\n","Cairo, allows for development of provable on-chain complexity":"카이로, 입증할 수 있는 복잡성 개발 참작\n","Careers":"경력","Carefully select the right equipment in the beginning of the round. You might want to shoot your enemies from a distance, or to burn the whole level to the ground – it’s your choice. Do you have what it takes to be master of destruction?":"라운드 시초에 올바른 장비를 신중하게 고를 것. 당신은 멀리서 당신의 적들을 쏘기 원하거나, 전 레벨을 바닥으로 태우기 원할지도 모릅니다 – 그것은 당신은 선택입니다. 당신은 파괴의 마스터가 되기 위해 취할 것을 가지고 있나요?\n","Choose Alliance":"동맹 선택하기\n","Choose Wisely!":"현명하게 선택하기!\n","Choose the best outfit that expresses your inner self. Unlock and collect multiple cosmetic skins and items that will carry across other games as interoperable NFTs.":"자신의 내면을 표현하는 최상의 의상을 선택합니다. 다른 게임을 넘어 상호 운용 가능한 NFTs로 가져갈 여러 복수의 화장품 스킨과 아이템을 열어 수집합니다. \n","Choose the best outfit that expresses your personality. Unlock and collect multiple cosmetic skins and items that will carry over to other games as interoperable NFTs.":"당신의 개성을 표현하는 최상의 의상을 선택합니다. 상호 운용 가능한 NFTs로서 다른 게임으로 이어지게 될 복수의 화장품 스킨과 아이템을 열어 수집합니다.\n","Choose the game mode that best suits your playstyle and fight for your right to party! Play alone or with friends and collect endless rewards.":"당신의 플레이 스타일에 가장 잘 어울리는 게임 모드를 선택하여 자기편의 권리를 위해 싸울 것! 혼자 아니면 친구들과 플레이하고 무한 보상을 수집합니다.\n","Choose your champion":"당신의 챔피언 선택하기\n","Claim All":"전체 청구하기\n","Claim rewards":"보상 요구하기\n","Claim your NFT reward":"당신의 NFT 보상 청구하기\n","Claimed":"청구됨\n","Co-Founder - CEO":"공동 설립자 – CEO\n","Co-Founder - CTO":"공동 창설자 - CTO","Co-Founder - Head of Blockchain":"공동 설립자- 블록 체인 헤드\n","Co-Founder - Operations":"공동 창설자 – 가동","Co-Founder, VP Games":"공동 설립자, VP 게임\n","Collect ALL":"전체 수집하기\n","Collect REWARDS":"보상 수집하기\n","Combine the right amount of acceleration, handling, and looks to nail the right recipe for the current track. You can also sprinkle in a bit of armour – just to play it safe – because, as my grandma always says, “if you ain’t ready for some rockets down the road, boy, you better come home!”":"현 트랙에 대한 올바른 방안을 찾기 위해 가속화, 취급 및 모습의 적절한 양을 결합할 것. 당신은 또한 약간의 갑옷을 입고 섞을 수 있습니다-단지 안전하게 게임을 할 뿐 – 왜냐하면, 나의 할머니가 항상 말하였듯이, “만약 네가 앞으로 약간의 로켓이 준비되어 있지 않다면, 너는 집에 가는 편이 낫단다!”\n","Coming Soon":"개봉 박두","Community":"커뮤니티","Compete, survive, win and earn!":"경쟁하고, 살아남고, 이기고 벌어라!\n","Complete":"완수하라\n","Complete privacy, not only from other users, but also from the Operator.":"프라이버시를 다른 사용자들뿐아니라 운영자로부터 완수할 것.\n","Compliance support":"컴플라이언스 지원\n","Configure your node and view your rewards in the dashboard":"당신의 노드를 설정하여 대시보드 안에서 당신의 보상을 볼 것.\n","Confirm password":"패스워드 확인\n","Confirm password is required!":"패스워드가 필수인지 확인할 것!\n","Congratulations!":"축하합니다!\n","Connect Wallet":"지갑 연결\n","Connect to your wallet to enter the Myriaverse":"미리아버스에 들어가기 위해 당신의 지갑을 연결할 것\n","Connect with us":"우리들 과의 연결","Connecting the world through <0>play</0>":"<0>플레이</0>를 통하여 세상과 연결하기","Connecting the world through play":"플레이를 통하여 세상과 연결하기","Contact Us":"당사 연락처","Contact us":"당사 연락처","Contributing to the future of a decentralized internet":"분권화된 인터넷 장래에 대한 기여\n","Copy invite link":"Copy invite link","Craig brings over 10+ years of experience in leading web & app UI/UX design and crafting brand identities. He has worked with global companies across industries such as communications, financial institutions, media and more.":"크레이그는 웹 & 앱 UI/UX 디자인을 리드하고 브랜드 아이덴티티를 만드는데 10년 이상의 경륜을 제공합니다. 그는 통신, 금융 기관, 미디어 및 그 이상의 산업에 해당되는 글로벌 기업들과 함께 작업해 왔습니다.","Create an account":"계정 창출\n","Creating the new standard for blockchain games and experiences":"블록 체인 게임 및 경험을 위한 새로운 기준 만들기\n","Cricket legend AB de Villiers has joined forces with Myria to deliver the next generation of blockchain gaming to cricket fans around the world":"Cricket legend AB de Villiers has joined forces with Myria to deliver the next generation of blockchain gaming to cricket fans around the world","Current Node Cycle":"현재의 노드 사이클\n","Current Openings":"현재의 개통\n","Customize your Moon Village with a range of cosmetic skins for various buildings and crew. These skins are NFTs, therefore remain yours forever, and are reusable across games.":"다양한 빌딩과 크루에 대해 일정 범위의 화장품 스킨으로 당신의 문 빌리지를 주문 제작할 것.\n","Daily $MYRIA token rewards":"매일의 $MYRIA 토큰 보상","Dashboard":"대시보드\n","Date Registered":"등록 날짜\n","Dave has over 25 years of marketing and management experience. He previously led marketing at Banxa and Rockstar Games. Dave also founded Marley Spoon; taking the company from inception to IPO.":"데이브는 25년 이상의 마케팅 및 관련 경험이 있습니다. 그는 이전에 방사 및 록 스타 게임에서 마케팅을 주도하였습니다. 데이브는 또한 말리 스푼을 창설하여, 시작부터 IPO까지 회사를 세웠습니다.\n","Decentralize the network by providing computing resources":"자원 계산을 제공하여 네트워크를 분권화한다.","Details will be announced soon.":"세부 사항은 곧 공표될 것이다.","Discover more":"더 많이 발견함\n","Dive in, we dare you. You’ll find yourself gracefully descending from the sky while you survey the lands to select your ideal landing spot. Enjoy that moment of peace, because once your feet touch the ground you’ll be plunged into an intense battle for loot and powerful equipment.As expected, life in this world ain’t easy, and at given intervals of time the playable area of the map will shrink, pushing you closer to your enemies. Needless to say, they ain’t waiting for you in a bed of roses, so be prepared!":"몰두할 것, 우리는 감히 당신에게 말합니다. 당신은 자신의 이상적인 착륙 지점을 선택하기 위해 땅 조사를 하는 동안 당신은 스스로 하늘에서 우아하게 내려가는 자신을 발견하게 될 것입니다. 평화의 순간을 즐길 것, 왜냐하면, 일단 당신의 발이 땅이 닿으면, 전리품과 강력한 장비를 위해 강력한 전투로 뛰어들게 될 것입니다. 예상한 대로, 이 세상에서의 삶은 만만하지 않으며, 주어진 시간 간격 사이에, 지도의  경기하기 좋은 구역이 줄어들어, 당신을 적들에게 더 가까이 밀어 넣게 될 것입니다. 말할 필요도 없이, 그들은 꽃밭에서 당신을 기다리고 있지 않기에, 준비를 하기 바랍니다!\n","Do you want to link your wallet {0} to your Myria account?":["당신은 자신의 지갑을 ",["0"],"자신의 미리아 계정에 연결시키기 원하나요??\n"],"Don't have a wallet yet? <0/>Install Metamask below.":"아직 지갑이 없다고요? <0/>아래에 메타마스크를 설치할 것.\n","Drift around thrilling tracks in a race for the finish line. Have endless fun shooting at your opponents and wreaking havoc all around you to gain extra points and climb the leaderboards. Dive into our numerous game modes and tracks to claim your rightful spot among the racing legends of the Myriaverse.":"결승선을 향해 경주에서 스릴 넘치는 트랙 주위를 이동할 것. 추가 점수를 얻고 리더 보드에 오르기 위해 당신의 적들을 쏘고 당신 주위에 있는 혼란을 입히는 끝없는 재미를 즐기기 바랍니다. 미리아버스의 레이싱 전설 가운데 당신의 합법적인 장소를 주장하고자 우리의 무수한 게임 모드와 트랙으로 뛰어들 것.\n","Drift it up!":"이동하기!\n","Driven by the search for knowledge, the Equinox collect every piece of information they can find. Somewhere in all this data must be the answer to peace. You might be the one to finally unlock that puzzle.":"지식 검색에 의해 몰려, 에퀴녹스는 그들이 재미있다고 느낄 수 있는 모든 지식의 단편들을 수집합니다. 이 모든 데이터 어디에선가, 평화에 대한 대답이 틀림없이 존재합니다. 당신은 그 퍼즐을 결국에는 풀 자 일지도 모릅니다.\n","Each node is allowed to vote on proposed changes to the Myria chain and nodes will also hold greater voting power throughout the Myriaverse":"각 노드는 Myria 체인에 제시된 변화에 대해 투표하는 것이 허용되고 노드는 또한 Myriaverse를 통하여 더 큰 투표 권한을 유지할 것이다.","Earn {point} points  when you send a message on the Myria Discord #suggestions channel that receives at least 20 reactions that are of the same type (e.g. 20 thumbs up reactions)":["Earn ",["point"]," points  when you send a message on the Myria Discord #suggestions channel that receives at least 20 reactions that are of the same type (e.g. 20 thumbs up reactions)"],"Earn {point} points by completing account creation":["Earn ",["point"]," points by completing account creation"],"Earn {point} points by following @myriagames on Twitter":["Earn ",["point"]," points by following @myriagames on Twitter"],"Earn {point} points by following myriagames on Instagram":["Earn ",["point"]," points by following myriagames on Instagram"],"Earn {point} points by joining the Myria Discord server":["Earn ",["point"]," points by joining the Myria Discord server"],"Earn {point} points by reaching level 40 on Myria Discord server and acquiring the ‘Space Lord’ activity role":["Earn ",["point"]," points by reaching level 40 on Myria Discord server and acquiring the ‘Space Lord’ activity role"],"Earn {point} points by sharing a tweet about Myria":["Earn ",["point"]," points by sharing a tweet about Myria"],"Earn {point} points for each day you send a message to any Myria Discord channel":["Earn ",["point"]," points for each day you send a message to any Myria Discord channel"],"Earn {point} points for each successful friend invite. You will earn {point} points each time a friend completes  both Alliance selection and Myria account creation via your invite link":["Earn ",["point"]," points for each successful friend invite. You will earn ",["point"]," points each time a friend completes  both Alliance selection and Myria account creation via your invite link"],"Earn {point} points when you add a thumbs up reaction to a message on the #myria-lore channel":["Earn ",["point"]," points when you add a thumbs up reaction to a message on the #myria-lore channel"],"Earn {point} points when you send your first message on any Myria Discord channel":["Earn ",["point"]," points when you send your first message on any Myria Discord channel"],"Easy to play, difficult to master. Select the right terrain to base your industrial revolution. Choose wisely, as this will determine the types of resources and production opportunities available to you. Make sure to look after your base in order to increase your score and climb the ranks. At the end of each tournament, the competition is reset, so don’t worry if you get it wrong the first time!":"게임 하기 쉽고, 마스터하기는 힘듦. 올바른 지형을 선택하여 당신의 산업 혁명을 근거지로 둘 것. 현명하게 선택할 것, 왜냐하면, 이것은 당신에게 이용 가능한 자원 유형과 생산 기회를 결정해 줄 것이기 때문이다. 당신의 점수를 증대시키고 등급을 올리기 위해 당신의 근거지를 반드시 돌볼 것. 각 시합 끝에, 경쟁이 리셋 되므로, 당신이 처음에 잘못될 까봐 걱정하지 말 것!\n","Ecosystem":"생태계","Education and support":"교육과 지원","Eliminate your opponents in this fast-paced-action-packed-survival game, to achieve victory and claim the spoils of war. There can be only one hero or team standing when the crimson curtain hits the ground.":"승리를 달성하고 전쟁 성과를 주장하기 위해 이러한 빠른 속도의 행동과 단단한 생존 게임에서 당신의 적들을 제거할 것.크림슨 커튼이 바닥을 치면 오직 한 명의 영웅 팀 만이 대기해 있을 수 있습니다.\n","Email":"이메일\n","Email address":"Email address","Email is required!":"이-메일이 요구됨!","Empower mainstream adoption to Ethereum using Myria’s scaling solution":"미리아 스케일링 솔루션을 사용하는 이더라 움에 주류 채택 권한을 부여할 것.\n","Empowering players":"선수들에게 권한 부여","Enabling digital asset scalability on Ethereum":"Enabling digital asset scalability on Ethereum","Enabling true scalability while leveraging the Ethereum network":"이더리움 네트워크에 영향을 미치는 동안 확장성 가능하게 하기 \n","Endless online fun":"무한한 온라인 재미\n","Enhancing the power of play through blockchain":"블록 체인을 통하여 게임 능력 향상시키기\n","Enter a password":"패스워드 입력\n","Enter the new era of web3.0 with confidence":"확신을 가지고 웹 3.0 새로운 시대 입력\n","Enter your email":"이메일 주소 입력\n","Enter your email address":"당신의 이 메일 주소 입력","Enter your email address to reset your password":"패스워드를 리셋하기 위해 이메일 주소 입력하세요.\n","Enter your password":"패스워드 입력\n","Equinox pursue balance in the Myriaverse and believe perhaps humanity and the Rift can coexist. They actively explore the Rift, searching for answers.":"미리아버스에서 에퀴녹스 추구 균형 및 아마도 휴머니티를 믿게 되면 리프트가 공존할 수 있습니다. 그것들은 활발하게 리프트를 개발하여, 해답을 찾게 됩니다.\n","Ethereum-level security provided by zk-STARKs":"zk-STARKs가 제공하는 이더리움 수준의 보안\n","Every hero has three unique abilities, each requiring charges, as well as one ultimate ability that requires charging through kills, deaths, collectibles, or objectives. It’s easier than it sounds, and even funnier…":"모든 영웅은 세 개의 고유한 능력을 갖추고 있는데, 각각은 충전을 해야 하고 또한 살인, 죽음, 수집 대상물 혹은 목표를 통하여 충전을 요하는 하나의 궁극적 능력을 요함. 그것은 들리는 것보다 훨씬 더 쉽고 더 재미있기까지 함…\n","Exclusive NFTs":"독점적 NFT","Experience low transaction fee and high throughput rate of up to 9000 transactions per second with our Myria scaling solution.":"우리의 미리아 스케일링 솔루션으로 초당 9,000 거래까지 낮은 거래 수수료와 높은 처리율을 경험하기 바랍니다.\n","FOLLOW US ON TWITTER":"트위터에서 우리를 따를 것.","FOR DEVELOPERS":"개발자용\n","FOR GAMERS":"게이머용\n","Fast transaction confirmation times provided by the L2 chain, while zk proofs and L1 data availability provide Ethereum-level security.":"L2 체인이 제공하는 빠른 거래 확인 시간, 반면 카 프루프 및 L1 데이터 이용 가능성은 이더리움 수준의 보안을 제공합니다.\n","Features":"특성\n","Filter by Department":"부분별 여과\n","First name":"이름\n","First name is required!":"이름 필수!\n","Follow on Instagram":"Follow on Instagram","Follow on Twitter":"Follow on Twitter","For Developers":"개발자용\n","Forgot your password?":"패스워드를 잊어버렸나요?\n","Founded by leading gaming and blockchain industry veterans":"주도적인 게이밍 및 블록 체인 산업 베테랑에 의해 창설됨","Free Sigil NFT":"무료 시길NFT\n","From wallet":"지갑에서\n","Full ownership of assets, non-custodial architecture":"온전한 자산 소유권, 비-구금형 아키텍처\n","Fully open-source and community-driven development":"완전한 오픈-소스 및 커뮤니티-기반 개발\n","GET A NODE":"노드 획득","GET MORE LICENSES":"더 많은 라이선스 획득\n","Game NFT":"게임 NFT","Gameplay first":"게임 플레이 먼저","Games":"게임","Games & Applications":"게임 & 애플리케이션\n","Gaming is evolving. Build with Myria!":"게이밍이 진화하고 있음. 미리아로 만드세요!\n","Gaming is evolving. Build with Myria.":"게이밍이 진화하고 있음. 미리아로 만드세요!\n","Gas Fee":"가스 비\n","Gather and upgrade the best equipment on the battlefield. Collect power-ups and build combat facilities to overcome your foes. You can deal with this harsh reality alone or with your friends in a team of three.":"전투지에서 최상의 장비를 모으고 업그레이드 할 것. 파워-업을 모아 전투 능력을 정립하여 당신의 적들을 극복할 것. 당신은 이 가혹한 현실을 혼자 아니면 세 개의 팀에서 당신의 친구들과 함께 다룰 수 있습니다.\n","General":"일반\n","General purpose zk-rollup":"일반 목적의 zk-롤업\n","Get Started":"시작하기\n","Get a founders node today":"오늘 창설자 노드 획득","Get started with your Myria node.":"당신의 미리아 노드로 시작하기.\n","Go back":"돌아가기\n","Go to my dashboard":"나의 대시보드로 갈 것.\n","Got an innovative gaming idea? Apply for a grant to manifest your vision on the Myria chain.":"혁신적 게임 아이디어를 얻었다고? Myria 체인 상 당신의 비전을 나타내기 위해 보조금 신청한다.","Grow with us and be rewarded. A Myria Node License allows you to operate a node from your home computer to support the growth of Myria’s decentralized network. As a node owner, you’ll earn daily rewards and exclusive benefits for your contribution.":"우리들과 함께 성장하여 보상을 받을 것. 미리아 노드 라이선스는 당신이 당신의 가정용 컴퓨터에서 노드를 작동시킬 수 있고 미리아의 분권화된 네트워크 성장을 지원할 수 있습니다. 노드 소유자로서, 당신은 매일 보상과 당신의 기여로 독점적인 혜택을 받게 될 것입니다.\n","Heroes don’t all wear capes":"영웅들은 모두가 다 케이프를 착용하지는 않습니다.\n","Home":"홈","How can I become a Myria node operator?":"나는 Myria 노드 운영자가 어떻게 될 수 있는가?","How it works":"그것은 어떻게 작동할까?","How many Myria node licenses will be released?":"얼마나 많은 Myria 노드 라이선스가 출시될까?","How much will a Myria node license cost?":"Myria 노드 라이선스 얼마나 많은 비용이 들까?","I am now a $MYRIA node owner! Join us to earn rewards, govern and build the future of virtual worlds! @MyriaGames":"나는 이제 $미리아 노드 소유자입니다! 보상을 받기 위해 우리들에게 가입하고, 가상 세계의 미래 지배하고 세우기 바랍니다! @MyriaGames\n","I just claimed a Rare Alliance Sigil on Myria":"나는 방금 미리아에서 래어 얼라이언스(희귀 동맹)을 청구하였습니다.\n","IN DEVELOPMENT":"개발 내\n","If you want to make a real impact in gaming and blockchain, join us at Myria. We’re scouring the world for the best and brightest to join our rapidly growing company.":"만일 당신이 게이밍과 불록 체인에 진정한 영향을 미치기 원한다면, Myria에서 우리들에게 가입할 것이다. 우리는 우리의 급속하게 성장하는 기업에게 합류하여 최상의 가장 밝게 세상을 탐색하고 있다.","In his role leading the People team, Damian helps Myria connect, develop and care for its global team. Damian brings decades of experience to Myria and was previously Global Head of People for Bitmex and prior to that a Managing Director in HR at Goldman Sachs.":"피플 팀을 이끄는 그의 역할에서, 다미안은 미리아가 자산의 글로벌 팀을 연결, 개발 및 돌보는데 도움을 줍니다. 다미안은 미리아에 수십 년간의 경험을 살렸고 이전에는 비트멕스 글로벌 피플 수장이자 골드맨 삭스의 HR 관리 책임자였습니다.\n","In this lawless racing game, you’ll need to do whatever it takes to thwart your rivals’ attempts at reaching the finish line before you. Choosing the optimal combination of driver and kart parts could be the difference between triumph and defeat. But don’t worry if your ride isn’t souped-up, you still have your shot at 1st place. Just make good use of the vicious power-ups scattered around the track to literally blast your competition into oblivion!":"이러한 무법 레이싱 게임에서, 당신은 당신보다 먼저 결승선에 도달하기 위한 당신 라이벌들의 시도를 좌절시키기 위해 할 수 잇는 일이라면 무엇이든 할 필요가 있을 것입니다. 운전자와 소형 자동차 파트의 적절한 결합 선택은 승리와 패배의 차이가 될  것입니다. 그러나, 당신의 타기가 자극적이지 않다 해도 걱정하지 마시길, 당신은 여전히 1순위로 여유가 있습니다. 당신의 경쟁을 문자 그대로 망각으로 날려 보내기 위해 트랙 주변에 흩어진 악랄한 파워-업을 잘 활용만 하기 바랍니다!\n","In this part of the Myriaverse, visitors are transformed into Bobble-Head form. This is according to this world’s terms of use and the laws of the metaverse that can be only broken by the mysterious Administrators...but more on that later.":"미리아버스의 이 부분에서, 방문자들은 버블-헤드 형식으로 변합니다. 이억은 이 세계의 사용 조건과 신비로운 관리자들에 의해서만 파괴 가능한….하지만 그 후에는 그 이상도 가능한 메타 버스의 법칙에 따른 것입니다.\n","Inclusion of at least one special character":"적어도 한 개의 특수 캐릭터 포함\n","Infrastructure & Tools":"기반시설 & 도구\n","Install Metamask":"메타 마스크 설치\n","Instant project visibility":"즉각적인 프로젝트 가시성\n","Instantly able to withdraw assets from the rollup back into ethereum when needed":"필요 시 롤업 백에서 이더리움으로 자산을 바로 인출 가능\n","Instantly reach and build an engaged player community of evangelists around your project through the Myria platform and network partners.":"미리아 플랫폼 및 네트워크 파트너들을 통하여 당신의 프로젝트 주변에 전도사들의 분주한 선수 커뮤니티에 바로 도달하여 수립할 것.\n","Intense battle to the very end":"바로 끝까지 강력한 전투\n","Interoperability FTW! Yes, we’ve got you covered - you can bring those crazy skins, sprays, hats, and every other exclusive NFT you’ve gathered along your journey through the Myriaverse. The last thing I want to see on the battlefield is tie-dye tights, but hey – it’s your party after all!":"상호 운영 가능성FTW! 예, 우리는 당신이 다룬 것을 얻었고 – 당신은 미리아버스를 통해 당신의 여정을 따라 당시이 수집한 모든 다른 독점 NFT, 미친 스킨, 스프레이, 모자 등을 가져올 수 있습니다. 내가 전투에서 보기를 원하는 마지막은  홀치기-염색 타이츠 이지만, 이봐요, 어쨌든 그것은 당신의 파티예요!\n","Invalid email!":"무효인 이 메일!","Inventory":"재고 목록\n","Invite your friends to Myria by sharing the following link with them.":"Invite your friends to Myria by sharing the following link with them.","It was a calm day in the land of candy hammers and fiery strawberry lakes when a mysterious portal suddenly spawned a roaring crowd of brave players. Ready to rock, they started to roll down the hill of chocolate donuts in a chaotic race for the win.":"그날은 신비로운 포털이 갑자기 용감한 플레이어들의 아우성을 낳은 캔디 망치와 불타는 딸기의 땅에 있는 고요의 날이었습니다. 흔들 준비가 되어, 그들은 승리를 위한 혼돈의 경주에서 초콜릿 도넛의 언덕 아래로 굴러가기 시작하였습니다.\n","It’s all in your hands":"그것은 모두 당신의 손 안에 있습니다!\n","Ivan has 10+ years of experience in the game industry. He has been a lead game designer at Gameloft, CrazyLabs, GPG (Ubisoft), Sperasoft, and more, with his games amassing billions of downloads.":"이반은 게임 산업에 10년 이상의 경력을 가지고 있습니다. 그는 게임로프트, 크레이지 랩, GPG(우비소푸트), 스페라소프트 그 이상에서, 수십억 건의 다운로드를 축적한 자신의 게임을 갖춘 게임 리드 디자이너입니다.\n","JOIN DISCORD":"DISCORD에 합류하기","JOIN NOW":"지금 가입할 것\n","JOIN THE TEAM":"팀에 합류하기","JOIN {sigilName}":["JOIN ",["sigilName"]],"Join MYRIA":"미리아에 가입하기\n","Join Myria in building the next blockchain revolution":"다음 블록체인 혁명을 건설하는데 미리아 가입하기\n","Join Vector Prime and you will be helping the Rift digitize worlds and all who live on them. Many factors will fight against you, but you know that science and logic are the best laws to rule us all.":"벡토르 프라임에 가입하면 당신은 리프트가 세상과  그들에 의존해 살고 있는 모든 이들을 디지털화 하는데 돕게 될 것입니다. 수많은 요소들이 당신과 대항에서 싸우게 되겠지만, 당신은 과학과 논리가 우리 모두를 지배할 최상의 법칙 임을 알게 됩니다.\n","Join our community!":"우리의 커뮤니티에 합류하시기를!\n","Join the Federation alongside mercenaries, militants, and anyone with a weapon and the desire to knock the Rift back to where it came from. There might be a place for you here, if you’re dedicated enough to fight until the very end.":"Join the Federation alongside mercenaries, militants, and anyone with a weapon and the desire to knock the Rift back to where it came from. There might be a place for you here, if you’re dedicated enough to fight until the very end.","Just make sure you’re still alive till the end of the round or you’ll be spectating from the sidelines. And that means nobody will be able to appreciate your cute hot-dog suit that you’ve just brought over from a zany skirmish in the Metarush world.":"단지 당신은 그래도 마지막 라운드까지 살아 있거나 당신이 측면에서 반드시 지켜보게 될 것입니다. 그리고 그것은 그 누구도 당신이 메타러쉬 월드에서 엉뚱한 충돌로부터 가져온 당신의 귀여운 핫도그 수트를 평가하지 못할 것이라는 뜻입니다.\n","Kidono is an experienced partner lead with extensive experience managing external development engagements. He has worked with some of the largest game production companies, including Marvel, Tencent, Winkings and more.":"키도노(Kidono)는 외부 개발 약속을 관리하는 광범위한 경험을 가진 노련한 파트너 리드이다. 그는 Marvel, Tencent, Winkings 등등을 포함하는 몇몇 최대 게임 생산 기업들과 작업해 왔다.","LEARN MORE":"더 많이 배우기","LINK WALLET":"링크 지갑\n","LOGGING IN":"로그 인\n","Last name":"성\n","Last name is required!":"성은 필수임!\n","Latest Updates & News":"최신 업데이트 & 뉴스\n","Launch Discord":"Launch Discord","Launch with full support from our tokenomics experts":"우리의 토큰 경제 전문가들로부터 온전한 지원을 받고 시작하기 바랍니다.\n","Layer 2 scalability solution":"2층 확장성 솔루션\n","Learn more":"더 많이 배우기","Learn more about our job current openings and see how you can get involved!":"우리 잡 현재의 오프닝에 대해 좀 더 알아보고 당신이 어떻게 연관될 수 있는지 참조할 것!\n","Led by industry experts":"산업 전문가들에 의한 주도","Leverage prebuilt Myria platform and tools - we take care of the supporting tools so you can focus on building your business.":"사전수립된 미리아 플랫폼 및 툴 지렛대 – 우리는 지지 툴을 돌보아 당신이 자신의 사업을 수립하는데 집중할 수 있습니다.\n","Limited supply available":"이용 가능한 제한된 공급\n","Locked":"Locked","Log in":"로그 인\n","Log out":"로그 아웃\n","Logout of your Myria account. Your progress will be accessible in future by logging into your Myria account via your wallet.":"Logout of your Myria account. Your progress will be accessible in future by logging into your Myria account via your wallet.","Low transaction fees":"낮은 거래 수수료\n","MINT ALL":"전부 주조\n","MINT SELECTED":"선별 주조\n","MYRIA STUDIOS":"미리아 스튜디오\n","Make it out of this world!":"이 세계로부터 벗어나기!\n","Manage your NFT & digital assets":"당신의 NFT & 디지털 자산 관리\n","Manage your digital storefront and marketplace":"당신의 디지털 상점 및 시장 터 관리하기.\n","Market your project across our platforms":"우리의 플랫폼을 넘어 당신의 프로젝트 마케팅 하기\n","Maximise your network":"당신의 네트워크를 최대화할 것\n","Message":"메시지\n","Message is required!":"메시지가 요구됨!","Mint Assets":"자산 주조\n","More coming soon!":"좀 더 개봉 박두!\n","More details coming soon":"좀 더 세부 내용 개봉 박두","More power than just network support":"단순한 네트워크 지원 이상의 강력한 힘\n","Morphing NFTs™":"모핑 NFTs™","Morphing NFTs™ that adapt to your avatars":"당신의 아바타에 적응되는 모핑 NFTs™","Morphing interoperable NFTs is the new standard created by Myria. Players will be able to utilize their NFTs across different avatars, games and metaverses. Truly own your digital assets and collectibles.":"상호 운용 가능한 NFT 변경은 미리아가 창출하는 새로운 표준입니다. 플레이어들은  다른 아바타, 게임 및 메타 버스를 넘어 그들의 NFT를 이용할 수 있을 것입니다. 진정으로 당신의 디지털 자산과 수집품들을 소유하기 바랍니다.\n","Mute sound":"음 소거\n","My Node Licenses":"나의 노드 라이선스\n","Myria FAQ":"Myria 자주 묻는 질문","Myria Founder’s Node":"미리아 설립자 노드\n","Myria Game Platform":"미리아 게임 플랫폼\n","Myria Infrastructure":"미리아 기반시설\n","Myria NFT Marketplace":"미리아 NFT 장터\n","Myria Node License":"미리아 노드 라이선스\n","Myria Nodes":"미리아 노드\n","Myria Partners":"미리아 파트너들\n","Myria Studios":"미리아 스튜디오\n","Myria Studios is building a range of free-to-play AAA games spans across an entire interconnected ecosystem. Players experience intertwining lore, characters, and rich storylines that carry across each game world, and Myria tokens and NFTs can be used across the Myriaverse.":"미리아 스튜디오는 전체 상호 연결된 생태계를 넘어 일정 범위의 무료 플레이 AAA 게임 기간을 수립하고 있습니다. 플레이어들은 상호 섞인 구전, 캐릭터 및 각 게임 세계 너머 전달되는 풍부한 스토리라인을 경험한 다음, 미리아 토큰과 NFT는 미리아버스를 넘어 사용될 수 있습니다.\n","Myria Wallet":"미리아 지갑\n","Myria combines a gaming platform with applications, tools and scaling infrastructure needed to bring blockchain gaming to life.":"미리아는 게이밍 플랫폼을 블록체인 게이밍을 활성화시키는데 필요한 애플리케이션, 툴 및 스케일링 기반시설과 결합시킵니다.\n","Myria develops fun AAA games that are enhanced by the blockchain, not vice versa.":"Myria는 블록 체인에 의해 향상되나 그 반대는 불가능한 재미있는 AAA 게임을 개발한다.","Myria ecosystem fund":"Myria 생태계 기금","Myria facilitates with tokenomics consultancy, Play-to-Earn game design best practices to achieve your business objectives.":"미리아는 토큰 경제 컨설턴시, 플레이 및 벌기 게임 설계의 최상 실습을 용이하게 하여 당신의 사업 목표를 달성할 수 있습니다.\n","Myria has been built by an all-star team of over 150 people, united with a common vision of revolutionizing gaming with blockchain technology.":"Myria has been built by an all-star team of over 150 people, united with a common vision of revolutionizing gaming with blockchain technology.","Myria help project partners set up for success. <0/> about our easy-to-use, full stack solution and build with Myria today.":"미리아는 성공을 목표로 프로젝트 파트너들이 설정하도록 돕습니다. <0/>사용하기 쉬운, 풀 스택 솔루에 대하여 그리고 오늘 미리아와 함께 세웁니다. \n","Myria helps blockchain projects scale. Whether you’re an established crypto business or simply exploring new possibilities, Myria has a solution for you!":"미리아는 블록체인 프로젝트 규모에 일조합니다. 당신이 수립된 암호 사업체이거나 단순히 새로운 가능성을 단지 탐구하고 있을 뿐이라면, 미리아는 당신을 위한 솔루션을 소유하게 됩니다!\n","Myria is a blockchain gaming ecosystem powered by Myria’s Ethereum L2 scaling solution":"Myria is a blockchain gaming ecosystem powered by Myria’s Ethereum L2 scaling solution","Myria is a blockchain gaming ecosystem powered by the Myria blockchain.":"Myria는 Myria 블록체인에 의해 가동되는 블록 체인 게이밍 생태계이다.","Myria is a decentralised Ethereum Layer 2, built to empower digital assets, NFT and blockchain gaming.":"미리아는 디지털 자산, NFT 및 블록체인 게이밍에 권한을 부여할 목적으로 수립된 분권화된 이더리움 층 2에 해당됩니다.\n","Myria is a decentralised Ethereum Layer 2, built to empower digital assets, NFT, blockchain gaming and more.":"미리아는 디지털 자산, NFT 및 블록체인 게이밍에 권한을 부여할 목적으로 수립된 분권화된 이더리움 층 2에 해당됩니다.\n","Myria is a decentralised Ethereum Layer 2, built to scale digital assets, NFTs and blockchain gaming.":"미리아는 규모의 디지털 자산, NFT 및 블록체인 게이밍에 수립된 분권화된 이더리움 층 2에 해당됩니다.\n","Myria is supported by a network of community-powered nodes, who receive rewards for their contributions.":"Myria는 커뮤니티에 가동되는 노드 네트워크로 지원을 받으며, 이는 그들의 기부에 대한 보상을 받는다.","Myria is the backbone of blockchain gaming":"미리아는 블록체인 게이밍의 중추입니다.\n","Myria node license sales are anticipated to commence in Q2 2022 (subject to change).":"미리아 노드 라이선스 판매는 Q2 2022에 시작할 것으로 예상됩니다. (변경에 따름)\n","Myria node operators will receive daily $MYRIA token rewards for the nodes work.":"Myria 노드 운영자들은 노드 작업에 대해 매일 $MYRIA 토큰 보상을 받게 될 것이다.","Myria provides an end-to-end solution for developers and publishers, and help you unlock the blockchain potential of your business":"미리아는 개발자 및 공개자에 대한 끝에서 끝을 보는 솔루션을 제공하고 당신이 당신 사업의 블록체인 잠재적 가능성을 열도록 도와 줍니다.\n","Myria provides blockchain consultancy services on NFT sale, blockchain integrations and platform support.":"미리아는 NFT 판매에 관한 블록체인 컨설턴시 서비스, 블록체인 통합 및 플랫폼 지원을 제공합니다.\n","Myria | Connecting the world through play":"Myria/ 플레이를 통해 세상을 연결시킴","Myriaverse is the wider social metaverse connecting players, communities and guilds. Players will be able to quest, explore, craft, invest and so much more. The ever expanding Myriaverse is a virtual society and economy that offers bountiful earning opportunities for the brave adventurer.":"미리아버스는 플레이어들, 커뮤니티 및 길드를 연결시키는 더 광범위한 메타버스이다. 플레이어들은 탐구하고, 탐색하며, 투자 및 그 이상의 것을 할 수 있을 것이다. 지금까지 확장해온 미리아버스는 용감한 모험가들에게 풍부한 수익 기회를 제공하는 가상 사회 및 경제이다.","Myria’s <0/> uses ZK-Rollup to enable dApps to achieve unlimited scale, low transaction fees without sacrificing on security.":"dApps가 무한한 규모, 보안에 관한 희생 없이 낮은 거래 수수료를 달성할 수 있도록 미리아의 <0/>은 ZK-롤업을 사용합니다.\n","Myria’s <0>Ethereum L2 scaling solution</0> uses zero-knowledge technology (zk-STARKs) to “roll-up” or bundle thousands of L2 transactions into one single transaction. This then sends a validity proof back to the main blockchain, preserving the security of L1 Ethereum. Myria’s STARK-based zkrollup solution enable NFTs and dApps to achieve unlimited scale, low transaction fees without sacrificing on security.":"미리아의 <0>이더리움 L2 스케일링 솔루션</0>은 제로 지식 기술을 사용하여 (zk-스타크)  수 천개의 L2 거래를 한 개의 단일 거래로 “올리거나” 묶을 수 있습니다. 그런 다음 이것은 주된 블록체인으로 효력 증거를 발송하여, L1 이더리움 보안을 보존합니다. 미리아의 스타크-기반 zk 롤업 솔루션은 NFT와 dAAPs가 보안에 대한 희생 없이 무한한 스케일, 낮은 거래 수수료를 달성할 수 있게 해 줍니다.\n","Myria’s NFT store and marketplace infrastructure is purpose built for blockchain games and optimized to support the players journey.":"미리아의 NFT 상점 및 시장 터 기반 시설은 플레이어들의 여정을 지원하고자 블록체인 게임을 목적으로 세워지고 최적화 된 것입니다.\n","NFT Rewards":"NFT 보상\n","Name":"성명\n","Name is required!":"이름을 요구함!","Native support for core token types ERC20 and ERC721":"핵심 토큰 유형 ERC20 및 ERC721에 대한 자연스런 지원\n","News":"뉴스","No matter the shape and size of your avatar, use the same NFT on them all. Buy once, use on any character. Designed to be interoperable.":"당신의 아바타의 모양과 크기가 어떠하든, 그들 모두에 대해 동일한 NFT를 사용한다. 일단 구매하고, 모든 캐릭터를 사용한다. 상호운용되도록 설계된다.","No web wallet is detected, please ensure that you have one setup to claim your rewards.":"그 어떤 웹 지갑도 감지되지 않음, 당신이 자신의 보상을 청구하기 위해 한 가지 설정을 갖추고 잇는지 반드시 확인하기 바랍니다.\n","Nobody lives forever, and these heroes sure as hell don’t care – they literally die for the sake of fun. Dive into a series of short combat rounds to win a match of 5 vs 5. The attacking team must activate a bomb on a specified location on the map. If you’re on the defending side…you know what you need to do, right?":"그 누구도 영원히 살 수는 없으며, 이러한 영웅들은 지옥조차도 개의치 않고- 그들은 문자 그대로 재미 그 자체를 위해 죽습니다. 5 대 5 매치에 이기기 위해, 각 회마다 일련의 짧은 전투 속으로 뛰어들 것. 공격하는 팀은 지도에서 특정 장소에 폭탄을 활성화하여야 합니다. 만약 당신이 측면을 방어하지 못하면, 당신은 당신이 해야 할 일을 알게 되죠? 그렇죠?\n","Node owner rewards":"노드 소유자 보상","Node owners will also be rewarded with NFT drops and tokens based on their reputational score as node operators and general community members.":"노드 소유자들은 또한 노드 운영자 및 일반 커뮤니티 구성원으로서 자신의 명성 점수에 기반하여 NFT 드롭과 토큰으로 보상을 받게 될 것이다.","Node owners will be rewarded with exclusive NFT drops and daily MYRIA token rewards for their contribution to the network.":"노드 소유자들은 네트워크에 대한 자신의 기여 때문에 NFT 드롭 및 매일의 미리아 토큰 보상으로 보상을 받을 것입니다.\n","Node progress this cycle":"노드 이 사이클을 진행함.\n","Node purchase":"노드 구매\n","Nodes":"노드","Nodes are an integral part of the Myria universe, with dedicated lore, gaming access, and exclusive voting power granted to node owners.":"노드는  노드 소유자들에게 부여된 독점 선거권과 헌신적인 전승, 게이밍 접속을 갖춘 미리아 유니버스의 통합 부분에 해당합니다.\n","Not the face!":"얼굴에 하지 말 것!\n","OPEN":"OPEN","OUR SOLUTION":"우리의 솔루션\n","Off Chain Assets":"체인 자산을 벗어남\n","Once in a while, a portal opens to a special game world – the BR Arena.":"가끔씩, 포털이 특별한 게임 세상-BR 아레나-을 개장합니다.\n","Once you figure out the optimal production pipeline, the game will start yielding rewards for all your hard work. Are you curious how difficult is to produce a batch of Starberry Jam? What about Moonsteel? It surely can’t be that hard, right?":"일단 당신이 최적 생산 파이프라인을 이해하게 되면, 게임은 당신의 모든 노고에 대해 보상을 수확하기 시작할 것입니다. 당신은 딸기 잼 한 묶음을 생산하는 일이 얼마나 힘든지 호기심이 생기나요? 문 스틸에 대해서는? 확실이 그렇게 힘들지 않죠, 그렇죠?\n","Only 50,000 Node Licenses will ever be released. Prices will increase as nodes are sold, to ensure our early supporters are rewarded.":"단지 50,000 노드 라이선스 만이 출시될 것입니다. 우리의 초기 지지자들이 보상받음을 확인하고자 가격은 노드가 판매된 만큼 증가될 것입니다.\n","Only 55,000 Myria node licenses will ever be released.":"단지 55,000 Myria 노드 라이선스가 출시될 것입니다.","Open and permissionless":"열림 그리고 무허가 성\n","Our Ethereum L2 scaling solution provides reliability, scalability and security. Making it easy for developers to build and manage their projects.":"우리의 이더리움 L2 스케일링 솔루션은 신뢰성, 확장성 및 보안성을 제공합니다. 개발자들이 그들의 프로젝트를 수립하여 관리하기가 쉽게 만듦.\n","Our Experience":"우리의 경험","Our Principles":"우리의 원칙","Our Solutions":"우리의 솔루션\n","Our games":"우리의 게임","Our solution":"우리의 솔루션\n","Our team":"우리의 팀","Our vision":"우리의 비전","Own your metaverse identity":"당신의 메타버스 아이덴티티 소유","Own your metaverse identity. Anywhere. Any game.":"당신의 메타버스 아이덴티티 소유: 어디든. 모든 게임.","PURCHASE NOW":"지금 구매하기\n","Partner Dapps":"파트너 Dapps\n","Partner Experiences":"파트너 경험\n","Partner Games":"파트너 게임\n","Partner Lead":"파트너 리드","Password is required!":"패스워드는 필수!\n","Password must be mixture of letters and numbers":"패스워드는 문자와 숫자를 혼합하여야 합니다.\n","Password must contain at least one special character":"패스워드는 적어도 한 개의 특수문자를 포함할 것.\n","Password must have at least 8 characters—the more characters, the better":"패스워드는 적어도 8개 문자 이상이되 – 글자 수가 많으면 더 좋습니다.\n","Password must have both uppercase and lowercase letters":"패스워드는 대문자와 소문자 둘다 있어야 합니다.\n","Pick one of the available warrior classes that best suits your playstyle. Are you a one-man army or a silent assassin who strikes from the shadows? Or maybe you’re a mysterious alien psychic who can suck the life out of his enemies...":"당신의 플레이 스타일에 가장 잘 어울리는 이용 가능한 전자 계급 중 하나를 고를 것. 당신은 그림자로부터 치는 무언의 암살자나 1인 군대 입니까? 아니면 아마도 당신은 자신의 적들로부터 생명을 빨아들일 수 있는 신비로운 외계인 초능력자일지 모릅니다.\n","Platform":"플랫폼\n","Please enter correct url!":"정확한 url을 입력하기 바랍니다!\n","Please view this experience on a desktop browser":"데스크톱 브라우저에서 이 체험을 보기 바랍니다.\n","Please view this experience on a supported browser like Chrome, Brave, Firefox or Edge":"크롬, 브레이브, 파어어폭스 혹은 에지와 같은 지원되는 브라우저에서 이 체험을 보기 바랍니다.\n","Points":"포인트\n","Powered and governed by <0>$MYRIA</0>":"<0>$MYRIA</0>에 의해 작동되고 지배됨","Powered by the community":"커뮤니티에 의해 작동됨","Powered by the community of player-run nodes":"플레이어 가동 노드 커뮤니티에 의해 작동","Pre register now":"이제 사전 등록","Pre register to stay up to date":"현재까지 사전 등록 유지","Price":"가격\n","Product Lead - Blockchain":"제품 리드 – 블록 체인","Provide your players with a protected experience and ensure your business understands critical regulatory compliance components.":"당신의 플레이어들에게 보호되는 체험을 제공하고 당신의 사업이 중대한 규정 부합 요소들을 이해하고 있는지 확인 바랍니다.\n","Pupose-built infrastructure":"목적-기반 시설","Purchase more nodes":"더 많은 노드 구매하기\n","Purchase more nodes to increase your rewards and passive invome":"당신의 보상과  수동적인 수입을 증대시키기 위해 더 많은 노드를 구매할 것.\n","Purchase now":"지금 구매하기\n","Quantity":"수량\n","Quantity: {qty}":["수량: ",["qty"],"\n"],"Quantity: {quantity}":["Quantity: ",["quantity"]],"REGISTER":"REGISTER","Read more":"더 많이 읽기\n","Receive $MYRIA and limited edition NFT rewards whilst supporting the Myria network":"$미리아와 한정판 NFT 보상을 미리아 네트워크를 지원하는 동안 수령할 것.\n","Refer your friends":"당신의 친구들을 언급할 것.\n","Register":"등록하기\n","Register to win exclusive<0/>AB de Villiers Cricket Game NFTs":"Register to win exclusive<0/>AB de Villiers Cricket Game NFTs","Releasing soon":"조만간 출시","Reliably programmable":"신뢰할 만큼 프로그램화 할 수 있음\n","Reset Password":"패스워드 재설정\n","Resistent against censorship":"검열에 대항함\n","Reward Vault":"보상 금고\n","Rewards":"Rewards","Rewards are coming soon! Information on how to install your node client will be released shortly.":"보상 개봉 박두! 당신의 노드 클라이언트를 설치하는 방법에 관한 정보가 곧 공개될 것입니다.\n","Roll with style!":"스타일에 대처하기!\n","Run a node and earn <0>$MYRIA</0> and NFT rewards":"노드 가동 및 <0>$MYRIA</0> 및 NFT 보상 받기","SELECT":"선택하기\n","SHIFTING POWER BACK TO THE PEOPLE":"사람들에게 동력을 돌려줄 것","START BUILDING":"건축 시작\n","SUBMIT":"제출","Save time. Ship faster.":"시간 절약. 더 빨리 선적하기.\n","Scale with security":"보안 규모\n","Scaling blockchain gaming on Ethereum":"이더리움에서 스케일링 블록체인 게이밍\n","Settings":"Settings","Share now":"지금 공유하기\n","Share on Twitter":"Share on Twitter","Share this on Twitter":"이것을 트위터에서 공유하기\n","Sign in":"서명 가입\n","Sign up":"등록하기\n","Sign up to our newsletter to receive development updates, token and NFT drops, and exclusive promotions.":"개발 업데이트, 토큰 및 NFT 드롭 및 독점 프로모션을 받고자 우리의 소식지에 등록하기.\n","Simpler formal verification of contracts due to safe VM architecture":"안전한 VM 구조로 인해 계약의 보다 더 형식적인 검증\n","Soon!":"조만간!\n","Sort By":"분류\n","Stand by":"대기\n","Start now":"지금 시작\n","Stay update to date with our project announcements and updates!":"우리의 프로젝트 발표 및 업데이트로 현재까지 업데이트 유지!\n","Step 1":"1단계\n","Step 2":"2단계\n","Step 3":"3단계\n","Step 4":"4단계\n","Store":"스토어","Subject":"주제\n","Subject is required!":"대상 필수!","Submit":"제출하기\n","Submitting...":"제출하기...\n","Supercharge your project":"당신의 프로젝트 과급\n","Support unlimited scalability, delivering up to 9,000+ TPS":"Support unlimited scalability, delivering up to 9,000+ TPS","TOKEN LAUNCH COMING SOON":"토큰 출시 개봉 박두>>>>>>> 원본/각색\n","Team":"팀","Terms | Privacy":"팀/프라이버시","Thank you for message. We will be in touch within 24-48 hours!":"메시지 감사드립니다. 우리는 24-48시간 내 연락 드리겠습니다.","Thank you for subscribing!":"구독해 주셔서 감사드립니다!","The Myria chain is supported by a network of player-run nodes. Use your home computer to become a node operator and receive rewards and benefits for your contribution to the ecosystem.":"Myria 체인은 플레이어 가동 노드 네트워크에 의해 지원된다. 당신의 가정용 컴퓨터를 사용하여 노드 운영자가 되고 생태계에 대한 당산의 기여로 보상과 혜택을 받을 것이다.","The Myria ecosystem is built on Myria blockchain, our Ethereum L2 built for gaming. Our team have developed proprietary technology to make the gaming and trading experience seamless, including a decentralized exchange, marketplace, and cryptocurrency wallet.":"Myria 생태계는 Myria 블록 체인에 기반하고, 게이밍에 대해서는 이더리움 L2에 기반한다. 우리 팀은 독점 기술을 개발하여 게이밍과 거래 경험을 아주 매끄럽게 만들고, 이에는 분권화된 교환, 마켓 플레이스 및 암호 화폐 지갑이 포함된다.","The Myria platform is supported by Myria Nodes. Being a <0/> operator means that you will receive limited edition NFTs, token rewards and more.":"미리아 플랫폼은 미리아 노드에 의해 지원됩니다. 운영자<0/>가 된다는 것은 당신이 한정판 NFTs, 토큰 보상 및 그 이상을 받을 것이라는 뜻입니다.\n","The comprehensive blockchain gaming platform":"통합 블록체인 게이밍 플랫폼\n","The easiest way to launch your blockchain game":"당신의 블록체인 게임을 시작하는 가장 쉬운 방법\n","The name must be between 3 and 16 characters long.":"The name must be between 3 and 16 characters long.","The new standard for interoperable NFTs":"상호 운용 가능한 NFT 용 신규 기준","The objective is simple – get to the finish line first and don’t fall to your doom while trying. Each match starts with a horde of players who get eliminated through a series of obstacles along the course to the final. The surviving players continue the battle in rounds with increasing difficulty until there is only one rushling standing.":"목표는 간단함 – 결승선에 도달하여 시도 중에 비운에 빠지지 않는 것임. 각 매치는 마지막 코스를 따라 일련의 장애를 통해 제거되는 한 무리의 플레이어들과 시작함. 살아남은 플레이어들은 각 라운드에서 대기하는 오직 하나의 러싱만 존재할 때까지 어려움이 점점 증대되면서 전투를 계속하게 됩니다.\n","The wallet currently connected does not match your designated reward wallet. Please connect the correct wallet or click below to change it.":"현재 연결된 지갑은 당신의 지정된 보상 지갑과 어울리지 않습니다. 정확한 지갑을 연결하거나 그것을 바꾸기 위해 아래를 클릭할 것.\n","The world’s first Morphing NFTs™":"세계 최초 모핑 NFTs™","Think before you shoot!":"당신이 사격하기 전에 생각할 것!\n","This decision is permanent. Once you choose your alliance you cannot change it!":"이 결정은 영구적입니다. 일단 당신이 자신의 동맹을 선택하면, 그것을 바꿀 수 없습니다!\n","Throw your prized vehicle around the deadly turns of the track as you throw a dice to decide your destiny. Drifting and performing crazy stunts is your way of expressing your inner speed demons, so don’t be shy– let them come out and play!":"당신이 자신의 운명을 결정하고자 주사위를 던지면 트랙의 치명적인 전환 주변으로 당신의 소중한 차량을 던질 것. 미친 스턴트를 행하고 이동하는 것은 당신의 내부 가속 악마를 표현하는 방식이기에, 부끄러워 하지 말고 – 그들로 하여금 나와서 플레이하게 할 것!\n","Time Online":"온라인 시간\n","Token Rewards":"토큰 보상\n","Tokenomics consultancy":"토큰 경제 컨설턴시\n","Total":"전체\n","Total $MYRIA Mined":"전체 $채굴된 미리아\n","Total NFT Rewards":"전체 NFT 보상\n","Total nodes in Myria network":"미리아 네트워크에 있는 전체 노드\n","Trailer Coming Soon":"트레일러 개봉 박두","Transaction List":"거래 목록\n","Transaction fee":"거래 수수료\n","Transparent and post-quantum secure -  no trusted setup":"투명하고 사후 퀀텀 확보 - 믿는 설정 금지\n","UI/UX Lead":"UI/UX 리드","Unified through the Myriaverse":"미리아버스를 통하여 통일됨","Unlock immense scale":"거대한 스케일 열기\n","Unmute sound":"소리 껐다 켜기\n","Unsupported Browser":"미 지원 브라우저\n","Username":"사용자 명\n","Username is required!":"사용자 명 필수!\n","Utilize your gunplay skills in a combination with your unique hero abilities to win a series of team battles or in other game modes. Manage your ability upgrades and weapons of choice to optimize your chances of winning.":"일련의 팀 전투나 다른 게임 모드에서 이길 수 있는 특별한 영웅 능력과의 결합으로 당신의 총 싸움 기술을 이용할 것. 당신의 능력 업그레이드와 선택 무기를 관리하여 이길 수 있는 확률을 최적화할 것.\n","VIEW ITEM":"아이템 보기\n","VIEW ITEMS":"아이템 보기\n","Vanity factor number 1":"자만심 요소 넘버 1\n","Vector Prime believe the Rift is the next step of evolution and will do anything in their power to accelerate its expansion.":"벡터 프라임은 리프트가 진화의 다음 단계이며 확대를 가속화하기 위해 그들의 힘으로 무엇이든 할 것입니다.\n","View job":"업무 보기\n","Voting rights":"투표권","Watch your village grow and flourish as you move up the ranks. Are you able to devise the best strategy and outperform your rivals?":"당신의 마을이 랭크를 이동함에 따라 성장하고 번성하는 것을 볼 것. 당신은 최상의 전략을 창안하여 당신의 경쟁자들을 능가할 수 있습니까?\n","We believe in open and equitable access, enabling more people to build, experience and enjoy the benefits of blockchain and the metaverse.":"우리는 열린 공정한 접속을 믿으며, 더 많은 사람들이 블록 체인과 메타 버스의 혜택을 수립하고, 경험하며 또 즐길 수 있습니다.\n","We believe players should have true verifiable ownership and control over in-game assets.":"우리는 플레이어들이 진실로 인증 가능한 소유권을 가지고 게임 내 자산에 대해 통제하여야 함을 믿는다.","We bring together everything that is required to build a decentralized, scalable and secure digital asset and blockchain gaming platform":"우리는 분권화 되고, 확장 가능하며 안전한 디지털 자산과 블록체인 게이밍 플랫폼을 반드시 수립하여야 하는 모든 것을 합칩니다.\n","We make digital asset trading and blockchain gaming easy with our all-in-one platform.":"우리는 우리의 일체형 플랫폼으로 디지털 자산 거래 및 블록체인 게이밍을 수월하게 만듭니다.\n","We make everything else easy so you can focus on your product":"우리는 모든 것을 쉽게 만들어 주기 때문에 제품에 집중할 수 있습니다.\n","We provide a full suite of blockchain infrastructure on the Myria chain, our Ethereum L2 built for gaming.":"우리는 Myria 체인에서 블록 체인 기반시설, 게임용 기반 이더리움 L2 전체 한 조를 제공한다.","We provide easy-to-use solutions to help projects kickstart their blockchain gaming journey":"우리는 프로젝트가 블록체인 게이밍 여정을 시동을 걸 수 있도록 돕고자 사용하기 쉬운 솔루션을 제공합니다.\n","We work closely with you from onboarding to deployment, provide advice from technical support to best practice recommendations to unlock new revenue streams and opportunities.":"우리는 신규 직원 교육부터 개발까지 당신과 긴밀하게 작업하며, 기술 지원으로부터 조언을 제공하여 최상의 실무 추천으로 새로운 수익 기류와 기회를 열게 됩니다.\n","We would love to hear from you. Contact us and get started with your blockchain gaming journey today.":"우리는 당신으로부터 직접 소식을 듣기 원합니다. 우리들에게 연락을 주시고 오늘 당신의 블록체인 게이밍 여정으로 시작하기로 합시다.\n","Welcome to the Myriaverse":"미리아버스로 오신 것을 환영합니다\n","We’re hiring, join the future of gaming":"우리는 고용하며, 게임의 미래에 합류할 것이다.","What are the software and hardware requirements to run a node?":"노드 가동에 필수 소프트웨어 및 하드웨어는 무엇인가?","What is a Myria node?":"Myria 노드는 무엇인가?","What rewards do Myria node operators receive?":"Myria 노드 운영자들은 무슨 보상을 받는가?","When will Myria node licenses be sold?":"Myria 노드 라이선스는 언제 판매될 것인가?","Which side of the battlelines will you stand on? <0/> Choose your Alliance and claim your free Sigil NFT.":"당신은 다음 전투에서 누구의 편에 설 예정인가요? <0/>당신의 동맹을 선택한 다음 당신의 공짜 시길 NFT를 청구할 것.\n","Whitepaper":"백서\n","With ships equipped with technology both ancient and cutting edge, the Federation Alliance patrols space, combating the expansion of the Rift on all fronts.":"고대 및 첨단 모두의 기술로 장착된 선박으로, 연합 동맹 정찰 스페이스, 모든 전면에서 리프트의 확장을 방지할 것.\n","Work with our experienced legal team":"당신의 노련한 법률 팀과의 작업\n","You are joining...":"당신은 합류하고 있습니다...\n","You have just claimed":"당신은 방금 청구하였습니다\n","You have opened":"당신은 열었습니다.\n","Your Myria Dashboard":"당신의 미리아 대시보드\n","Your NFT is your digital identity. Take your NFT with you on your metaverse explorations. Equip and level up in different games and worlds. Truly own your metaverse identity with Myria.":"당신의 NFT는 당신의 디지털 아이덴티티이다. 당신의 메타버스 개발에서 자신의 NFT를 취할 것이다. 다른 게임과 세상에서 장비를 갖추고 레벨을 높일 것이다. 진실로 Myria와 자신의 메타버스 아이덴티티를 소유할 것이다.","Your purchase is complete.":"당신의 구매가 완수되었습니다.\n","a decentralized ecosystem":"분권화된 생태계","join the revolution":"레볼루션에 합류 ","view now":"바로보기","{balance} ETH":[["balance"]," ETH"],"“Mirror, mirror on the wall, who's the fairest of them all?”\nIf you’re still wondering whether those shiny pink rims or the NFT unicorn sticker on the hood are going to make you stand out on the track, then wonder no more. You're gonna shine like a star as your opponents remain a distant glimpse in the rear-view mirror.":"“거울아, 거울아, 그들 중에서 누가 가장 희니?” 만약 당신이 아직도 그렇게 반짝이는 핑크 테두리나 후드에서 NFT 유니콘 스티커가 당신으로 하여금 트랙에서 눈에 띄게 한다면, 더 이상 궁금해 하지 말 것. 당신은 자신의 적들이 백미러에서 멀리 힐끗 보일 때, 별처럼 반짝일 것입니다.\n"}};
